const emailjs = require('./vendor/emailjs.min');

const contactForm = document.getElementById('contact_form');

emailjs.init('user_pVsXeBRyj0LMiCqzLjI32');

function sendM(submitEvent) {
  submitEvent.preventDefault();

  const formOk = document.getElementById('form_ok');
  const formError = document.getElementById('form_error');

  const overlay = document.getElementById('overlay');
  overlay.classList.toggle('hidden');

  emailjs
    .send('service_0vw3hso', 'template_mqt13g8', {
      fullName: document.getElementById('fullName').value,
      phone: document.getElementById('phone').value,
      email: document.getElementById('email').value,
      company: document.getElementById('company').value,
      platform: document.getElementById('platform').value,
    })
    .then((response) => {
      console.log('SUCCESS!', response.status);
      formOk.classList.remove('hidden');
      overlay.classList.toggle('hidden');
      contactForm.classList.add('hidden');
    })
    .catch((error) => {
      overlay.classList.toggle('hidden');
      contactForm.classList.add('hidden');
      formError.classList.remove('hidden');
      console.log('FAILED...', error);
    });
}

contactForm.addEventListener('submit', sendM);
